import { BrowserRouter as Router, Route, Switch } from 'react-router-dom';
import { AppProvider } from './components/context/AppContext';
import Home from './pages/Home';
import './styles/main.scss';

function App() {
  return (
    <AppProvider >
      <div className="App">
        <Router>
          <Switch>
            <Route path='/' exact component={Home} />
          </Switch>
        </Router>
      </div>
    </AppProvider>
  );
}

export default App;
